const API_KEY = 'e49c82872d0187f448d5b73880a3908b';
const API_BASE = 'https://api.themoviedb.org/3';

/*
-original de netflix
-recomendados
-mas votados
-accion
-comedia
-terror
-romance
-documentales
*/
//endpoint nos dice que titulo traer ejemplo
//segun el slug: originals, trending, accion, comedia, romance, etc

//async es una funcion pre establecida por js para hacer funcionces asincronas
//const function1 = async(){}

//async function function1 (){}

//async()=>{} -> estoy es una funcion anonima 

const basicFetch = async (endpoint) =>{
	const req = await fetch(`${API_BASE}${endpoint}`)
	const json = await req.json();
	return json
}

export default {
	getHomeList: async ()=> {
		return [
		{
			slug:'originals',
			title:'Original de netflix',
			items: await basicFetch(`/discover/tv?with_network=213&language=es&api_key=${API_KEY}`)
		},
		{
			slug:'trending',
			title:'Recomendados para vos',
			items:await basicFetch(`/trending/all/week?language=es&api_key=${API_KEY}`)
		},
		{
			slug:'toprated',
			title:'Mejores Calificados',
			items:await basicFetch(`/movie/top_rated?language=es&api_key=${API_KEY}`)
		},
		{
			slug:'action',
			title:'Accion',
			items:await basicFetch(`/discover/movie?with_genres=28&language=es&api_key=${API_KEY}`)
		},
		{
			slug:'comedy',
			title:'Comedia',
			items:await basicFetch(`/discover/movie?with_genres=35&language=es&api_key=${API_KEY}`)
		},
		{
			slug:'horror',
			title:'Terror',
			items:await basicFetch(`/discover/movie?with_genres=27&language=es&api_key=${API_KEY}`)
		},
		{
			slug:'romance',
			title:'Romance',
			items:await basicFetch(`/discover/movie?with_genres=10749&language=es&api_key=${API_KEY}`)
		},
		{
			slug:'documentary',
			title:'Documentales',
			items:await basicFetch(`/discover/movie?with_genres=99&language=es&api_key=${API_KEY}`)
		},
		]
	},
	getMovieFilm: async (movieId,type)=>{
		//tipo para filtrar si es serie o pelicula
		let info ={};
		if(movieId){
			switch (type) {
				case 'movie':
					info = await basicFetch(`/movie/${movieId}?language=es&api_key=${API_KEY}`)
					break;
				case 'tv':
					info = await basicFetch(`/tv/${movieId}?language=es&api_key=${API_KEY}`)
					break;
				default:
					info = null;
					break;
			}
		}
		return info;
	},

	getFilmAllMovies: async (movieId,type)=>{
		let info2={};
		let endpointYT = 'https://www.youtube.com/embed/'
			
		info2 = await basicFetch(`/${type}/${movieId}/videos?api_key=${API_KEY}`)
		const index = info2.results?.map(e=>e.name.toLowerCase().includes('trailer')).indexOf(true)
		if(index != -1){
			endpointYT = `${endpointYT}${info2.results[index].key}`
			return endpointYT
		}else if(info2.results.length > 0){
			endpointYT = `${endpointYT}${info2.results[0].key}`
			return endpointYT
		}

		
		
	}
}
