import React,{useEffect,useState} from 'react';
import Tmdb from './tmdb.js';
import MovieRow from './components/MovieRow.js';
import './App.css'
import FeaturedMovie from './components/FeaturedMovie.js'
import Header from './components/Header'

export default function() {

  const [movieList, setMovieList] = useState([])
  const [FeaturedData, setFeaturedData] = useState(null)
  const [blackHeader, setBlackHeader] = useState(false)


  useEffect(()=>{
    //funcion que recorre async con await para levantar los datos
    const loadAll = async ()=> {
      //pegando la lista total
      let list = await Tmdb.getHomeList();
      setMovieList(list);
      //pelicula Netflix - recomendados Header
      const original = list.filter((el)=> el.slug === 'originals')
      const randomChosen = Math.floor(Math.random() * (original[0].items.results.length))
      const chosenInfo = await Tmdb.getMovieFilm(original[0].items.results[randomChosen].id,'tv') //netflix es todo tv
      setFeaturedData(chosenInfo)
    }
    loadAll();
  },[]);

  useEffect(()=>{
    const scrollListener=()=>{
      window.pageYOffset >= 300 ? setBlackHeader(true) : setBlackHeader(false)
    }
    window.addEventListener('scroll', scrollListener)
    return ()=>{
      window.removeEventListener('scroll', scrollListener)
    }
  },[])
  
  return (

    <div className="page">

        <Header blackHeader={blackHeader}/>


      {
        !movieList || !FeaturedData ?

        <div className="loading">
          <img src="https://media.wired.com/photos/592744d3f3e2356fd800bf00/master/w_1920,c_limit/Netflix_LoadTime.gif%201920w,%20https://media.wired.com/photos/592744d3f3e2356fd800bf00/master/w_2240,c_limit/Netflix_LoadTime.gif" alt=""/>
        </div>  
        :
        <>

          {FeaturedData && <FeaturedMovie items={FeaturedData}/>}

          <section className="lists">
            {movieList.map((lista,i)=>(
                <MovieRow key={i} title={lista.title} items={lista.items} />        
              ))}
          
          </section>

          <footer>
            Hecho por <span role="img" area-label="corazon">❤</span> Mauricio Barrios
            <br/>
            Derechos de imagen de Netflix<br/>
            Datos sacados del sitio Themoviedb.org
          </footer>
         </>
      }

      
      

    </div>
    );  
}
