import React from 'react';
import './FeaturedMovie.css';
import EventSeatOutlinedIcon from '@mui/icons-material/EventSeatOutlined';


export default ({items})=>{
	const img=`https://image.tmdb.org/t/p/original${items.backdrop_path}`
	let year = new Date(items.first_air_date)
	let genre = items.genres.map((el,key)=> el.name);
	let create = items.created_by?.map((el,key)=> el.name);
	return (
		<section className="featured" style={{backgroundSize:'cover',backgroundPosition:'center',backgroundImage:`url(${img})`}}>
		
			<div className="featured--vertical">
				<div className="featured--horizontal">
					<div className="featured--name">{items.original_name}</div>
					<div className="featured--info">
						<div className="featured--point">{items.vote_average} Puntos</div>
						<div className="featured--year">{year.getFullYear()}</div>
						<div className="featured--seasons">{items.number_of_seasons} {items.number_of_seasons > 1 ? 'Temporadas':'Temporada'} </div>
					</div>
					<div className="featured--description">{items.overview}</div>
					<div className="featured--buttons">
						<a href="#" className="featured--watch"> ► Reproducir</a>
						<a href="#" className="featured--add">+ Añadir a Lista</a>
					</div>	
					<div className="featured--genres">
						<strong>Generos:</strong> {genre.join(', ')}
					</div>
					{/*<div className="featured--genres">
						<strong>Generos:</strong> {genre.join(', ')}
					</div>*/}
					<div className="featured--created">
					{
						create.map((el,i)=>{
							return(
								<>
									<div className="featured--created-items" key={i}>
										<EventSeatOutlinedIcon style={{fontSize:'40px'}}/>	
										<p>
											{el}
										</p>
									</div>
									{
										i != create.length -1 && <div key={i} style={{width: "2px",height:'60px',backgroundColor:'#666'}}></div>
									}
								</>
								)
						})
					}

					</div>
				</div>
			</div>
			
			
		</section>
		)


}


